.TodoSearch {
    background: #F9FBFC;
    border-radius: 25px;
    border: 2px solid #202329;
    display: block;
    width: 40%;
    margin: 0 auto;
    height: 50px;
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSearch:focus {
    outline-color: #217e05
}