form {
    width:  90%;
    max-width: 400px;
    background-color: #e6e0e0;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 30px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

textarea {
    background-color: #F9FBFC;
    border: 3px solid #202329;
    border-radius: 3px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
    resize: none;
}

textarea::placeholder {
    color: #A5A5A5;
    font-weight: 700;
    font-family: 'Montserrat';
}

textarea:focus {
    outline-color: #217e05;
}

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';


}

.TodoForm-button--cancel {
    background: transparent;
    background: #f70202;
    color: #F9FBFC;
}

.TodoForm-button--add {
    color: #F9FBFC;
    background: #217e05;
    box-shadow: 0px 5px 25px rgba(97, 218, 1250, 0.5);
}